
:root {
  /* 标题 */
  --title-color: rgba(0,0,0,0.85);
  /* 正文 */
  --text-color: rgba(0,0,0,0.65);
  /* 次要文字 */
  --sub-text-color: rgba(0,0,0,0.45);
  /* 不可点文字 */
  --disable-text-color: rgba(0,0,0,0.25);
  /* 线的颜色 */
  --line-color: rgba(0,0,0,0.10);
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  overflow: hidden;
  text-rendering: optimizeLegibility;
}

body {
  color: rgb(var(--foreground-rgb));
}

a {
  color: inherit;
  text-decoration: none;
}

:global {
  #root {
    width: 100%;
    height: 100%;

    @font-face {
      font-family: "qweather-icons";
      src: url("./assets/font/fonts/qweather-icons.woff2") format("woff2"),
      url("./assets/font/fonts/qweather-icons.woff") format("woff"),
      url("./assets/font/fonts/qweather-icons.ttf") format("truetype");
    }

    [class^="qi-"]::before,
    [class*=" qi-"]::before {
      content: '';
      display: inline-block;
      font-family: "qweather-icons" !important;
      font-style: normal;
